*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
}

body{
    margin: 0;
}

.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(23, 23, 23);
    padding: 25px 10px;
    font-family: 'Ubuntu', sans-serif;
    border-bottom: 1px solid white;
}

.navbar .logo a{
    color: #fff;
    font-size: 35px;
    font-weight: 600;
}

.menu{
    list-style: none;
    display: flex;
}

.menu a{
    padding: 0px 20px;
    color: #fff;
}
